
.bookingsTable {
  font-size: 1.7vh;
  vertical-align: top;
}

.bookingsTable td {
  max-width: 18em;
  padding: 1px 2px 3px 2px;
  height: unset;
  color: #000000a8;
  font-weight: normal;

  text-align: left;
}
.bookingsTable td:nth-of-type(1) {
  text-align: right;
  min-width: 1em;
}


.bookingsTable th {
  position: -webkit-sticky;
  position: sticky;
  left: 0px;

  border-right: solid white 1px;
  padding: 2px 2px;
  font-weight: bold
}

.bookingsTable pre {
    margin: 0;
    white-space: pre-wrap;
}

.bookingsTable .th0 {
  background-color: #D8E6F3;
  border-bottom: solid black 1px;
  z-index: 10;
  top: 0; left: 0;
}

.bookingsTable .th-header {
  top: 0px;
  height: 2.5vh;
  border-bottom: solid black 1px;
  background-color: #ffffff;
  color: Navy;

  padding: 0;
  vertical-align: middle;
  font-size: 2vh;
  text-align: center !important;
}

.bookingsTable .th-header:nth-of-type(4),
.bookingsTable .th-header:nth-of-type(5) {
  min-width: 10em;
}



.curDayDiv {
  position: absolute;
  top: 110%;
  left: 7%;
  width: 95vw;
  padding: 0.3em 0;
  box-shadow: 0px 4px 5px 1px rgba(0,0,0,0.7);

  font-size: 2vh;


  vertical-align: middle;
  background-color: #e0dede
}
.curDayDiv.HDay {
    background-color: #ffa1a1;
}


.newDayTr th,
.newDayTr td {
  height: 3vh;
  padding: 0.2em;
  border-top: 2px solid black;

  font-weight: bold;
  color: #464646;
  background-color: #ffffff;
}
.newDayTr th {
  z-index: 1;
}
.newDayTr.HDay th,
.newDayTr.HDay td {
  color: crimson;
}



.tdSpan {
  display: inline-block;
  color: #0000ff5c;
}

td.freeTimeTd {
  text-align: left !important;
  vertical-align: middle;
  padding: 0 0 0 0px;
  font-style: italic;
  color: #252dbf;
}


.noTime-book th,
.noTime-book td {
  background-color: #56565680;
}


.pastSlot th,
.pastSlot td {
  color: #0000006e;
  font-style: italic;
  background-color: #ffffffb5;
}




.bookingsTable  .groupN {
  background-color: #bfddf9;
}
.bookingsTable .groupN.odd {
  background-color: #cfedff;
}
.bookingsTable .groupD {
  background-color: #a8ff9e;
}
.bookingsTable .groupD.odd {
  background-color: #c9ffcb;
}

.bookingsTable .pastSlot {
    background-color: #f2f2f2;
}
.bookingsTable .pastSlot.odd {
    background-color: #ffffff;
}


.bt-serv-tbody {
    height: 100vh;
    text-align: center;
}

.bt-serv-tbody
td.bt-serv-td {
    color: #a00202a8;
    background-color: #fabcbca8;
    font-weight: normal;

    text-align: center;
    vertical-align: top;
}

.freeTimeButton {
    width: 13em;
    text-align: left;
}






.blinkElem {
  background-color: #a8ff9e !important;
  animation: change-background 1s linear infinite;
}
  @keyframes change-background {
    25%, 75% { background: #a8ff9e; }
    50% { background: #0600ff; }
  }
