body {
    font-family: sans-serif;
  }

  input {
    border: 1px solid #999;
    padding: 0.5rem;
    width: 300px;
  }

  .no-suggestions {
    color: #999;
    padding: 0.5rem;
  }

  .suggestions {
    border: 1px solid #2343ba;
    border-top-width: 0;
    list-style: none;
    margin-top: 0;
    max-height: 20em;
    overflow-y: auto;
    padding-left: 0;
    background-color: white;
    width: calc(300px + 1rem);
  }

  .suggestions li {
    padding: 0.5rem;
  }

  .suggestion-active,
  .suggestions li:hover {
    background-color: #8ec5f2;
    color: #03136a;
    cursor: pointer;
    font-weight: 700;
  }

  .suggestions li:not(:last-of-type) {
    border-bottom: 1px solid #2343ba;
  }
