@import './normalize.css';
@import './landScape.css';
@import './bookings.css';
@import './freetime.css';


body {
    margin: 0;
    font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
        'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
        sans-serif;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
}

body {
    background-color: rgb(228, 250, 132);
}

table {
    font-size: 1.5vh;
    font-family: "Lucida Sans Unicode", "Lucida Grande", Sans-Serif;
    border-spacing: 0;
    text-align: center;
    vertical-align: middle;

    width: 100%;
    white-space: nowrap;
    background-color: white;
}


button {
    height: 2em;
}


#contentWrapper {
    display:   inline-flex;
    max-width: 100%;
    overflow:  hidden
}

.buttonBar,
.bottomBar {
    width: 98vw;
    white-space: nowrap;
    display: inline-flex;
    margin: 0.5vw;
}

.navButton {
    width: 3em;
}

.reqDateButton {
    position: relative;
    min-width: 7em;
}

#reqDateInput,
#reqDateInput::-webkit-calendar-picker-indicator {
    position: absolute;
    top: 0;
    left: 0;
    margin-inline-start: 0;
    width: 95%;
    height: 95%;
    opacity: 0;
}

#reqDateInput::-webkit-datetime-edit { display: none;}


.inner {
    display: inline-flex;

    overflow-x: scroll;
    overflow-y: scroll;
    -webkit-overflow-scrolling: touch;


    margin: 0.5vw;

    max-width: 97vw;
    height: 84vh;

    border-style: solid;
    border-width: 3px;
    border-color: black;
    border-radius: 5px
}
