.flyersForm {
    position: relative;
    background: #f0f0f0;
    border-radius: 10px;
    padding: 20px;
    margin: 20px auto;
    width: 100%;
    max-width: 17em;
  }

  .flyersForm form {
    text-align: left;
  }

  .flyersForm label {
    display: block;
  }

  .flyersForm input,
  .flyersForm select {
    display: block;
    box-sizing: border-box;
    margin: 0 0 10px;
    padding: 5px;
    border: 1px solid #c0c0c0;
    border-radius: 5px;
    font-size: 16px;
    line-height: 20px;
    width: 100%;
  }

  .flyersForm input[name="flyerId"] {
    display: inline;
    margin-left: 5.5em;
    line-height: 1.5em;
    width: 7em;

    text-align: center;
    font-weight: bold;
  }

  .flyersForm input[name="Flight_Date"] {
    display: inline;
    width: 9em;
  }

  .flyersForm input[name="timeVal"] {
    margin-right: 2em;
    line-height: 1.5em;
    width: 5em;

    text-align: center;
    font-weight: bold;
  }

  .flyersForm select[name="TimeSlot"] {
    display: inline;
    margin-left: auto;
    width: 7em;
  }

  .flyersForm select[name="status"] {
    margin-left: auto;
    width: 10em;
  }




  .flyersForm__inlineWrapper{
    display: flex;
  }

  .flyersForm button {
    background: #0d718d;
    color: #fff;
    padding: 5px 20px;
    border: 1px solid #064758;
    border-radius: 5px;
    font-size: 16px;
    line-height: 20px;
    cursor: pointer;
  }


  .flyersForm .error {
      color: red;
      margin: -5px 0 10px;
    }




#flyersContainer {
    position: absolute;
    top: 1em;
    left: 1em;
    z-index: 100;
    width: 95vw;
    height: 95vh;
    background-color: #0330faa9;
    border-radius: 10px;
}


#flyerEditor-btn-block {
    display: flex;
    padding: 0;
    text-align: right;
}

#closeSidebarBtn {
    position: absolute;
    top: 3px;
    right: 3px;
    background-color: inherit;

    border-style: solid;
    border-radius: 6px;
    border-color: #0332fa;

    height: 1.75em;
    width: 3em;

    vertical-align: bottom;
    line-height: 0;
    font-weight: 700;
    color: #0332fa;
}



.flyersForm-header {
    margin-top: 2em;
    margin-bottom: -0.5em;
    margin-left: auto;
    margin-right: auto;
    border-radius: 10px;
    text-align: center;
    width: max-content;
    padding: 0.5em 2.5em;
    background-color: #0332fa;
    color:#f0f0f0;
    font-weight: bold;
    font-size: 1.5em;
}