.side-form {
    position: relative;
    background: #f0f0f0;
    border-radius: 10px;
    padding: 20px;
    margin: 20px auto;
    width: 100%;
    max-width: 17em;
  }

  .side-form form {
    text-align: left;
  }

  .side-form label {
    display: block;
  }

  .side-form input,
  .side-form select {
    display: block;
    box-sizing: border-box;
    margin: 0 0 10px;
    padding: 5px;
    border: 1px solid #c0c0c0;
    border-radius: 5px;
    font-size: 16px;
    line-height: 20px;
    width: 100%;
  }

  .side-form input[name="bookingId"] {
    display: inline;
    margin-left: 5.5em;
    line-height: 1.5em;
    width: 7em;

    text-align: right;
    font-weight: bold;
  }

  .side-form input[name="Flight_Date"] {
    display: inline;
    width: 9.5em;
  }

  .side-form input[name="timeVal"] {
    margin-right: 2em;
    line-height: 1.5em;
    width: 5em;

    text-align: center;
    font-weight: bold;
  }

  .side-form select[name="TimeSlot"] {
    display: inline;
    margin-left: auto;
    text-align: right;
    width: 7em;
  }

  .side-form select[name="status"] {
    margin-left: auto;
    width: 10em;
  }




  .side-form__inlineWrapper{
    display: flex;
  }

  .side-form button {
    background: #0d718d;
    color: #fff;
    padding: 5px 20px;
    border: 1px solid #064758;
    border-radius: 5px;
    font-size: 16px;
    line-height: 20px;
    cursor: pointer;
  }

  #SubmitBlock {
    text-align: center;
  }

  #SubmitBlock>button {
    margin-top: 1.5em;
    width: 100%;
    border: none;
    font-weight: bold;

  }
  #SubmitBlock>button[name = 'toCancel'] {
    background-color: rgb(255, 83, 83);
  }




  .side-form .error {
      color: red;
      margin: -5px 0 10px;
  }


#bookingEditor-btn-block {
    display: flex;
    padding: 0;
    text-align: right;
}

#closeSidebarBtn {
    position: absolute;
    top: 3px;
    right: 3px;
    background-color: inherit;

    border-style: solid;
    border-radius: 6px;
    border-color: #0332fa;

    height: 1.75em;
    width: 3em;

    vertical-align: bottom;
    line-height: 0;
    font-weight: 700;
    color: #0332fa;
}

#sidebar {
    position: relative;
    /* display: inline-flex; */
    overflow-x: scroll;
    /* overflow-y: scroll; */
    -webkit-overflow-scrolling: touch;




    margin: 0.5vw;
    padding: 0 3px;

    min-width: 20em;
    height: 84vh;

    border-style: solid;
    border-width: 3px;
    border-color: rgb(3, 50, 250);
    border-radius: 5px;

    background-color: rgb(123, 147, 253);
}

.sidebar-header {
    margin-top: 0em;
    margin-bottom: -0.5em;
    text-align: left;
    padding-left: 0.5em;
}


